import axios from "axios";
import store from "@/store"

async function join(user) {
    try {
            const response = await axios.post("/member/join", { //아래의 값들은 dto의 값들과 순선, 이름 일치 해야 한다. 
            mid: user.id,
            mname: user.name,
            mpassword: user.password,
            mrole: user.role,
            memail: user.email
            });
        return response.data.result; //spring의 결과갓이 리턴된ㄷ.ㅏ //success, duplicated, fail이 반환된다. 
    } catch (error) {
        console.log(error);
        return "fail-network";
    }
}

async function login(user) {
    try {
        const response = await axios.post("/member/login", {
            //아래의 값들은 dto의 값들과 순선, 이름 일치 해야 한다.
            mid: user.id,
            mpassword: user.password
        });
        store.dispatch("saveAuth", { userId: response.data.mid, authToken: response.data.accessToken });
        return "success";
        } catch (error) {
        console.log(error);
        if (error.response) { //network에러면 에러 메시지가 없다. 
            if (error.response.status === 401) {
                return "fail-401";
            } else {
                return "fail-network";
            }
        }
    }
}

async function logout(user) {
    
    try {
        await axios.get("/member/logout"); //spring 서버단의 get(logout)과 일치시킨다.
        store.dispatch("deleteAuth"); //store index의 delteAuth사용 즉 값을 삭제 시킨다. 
    } catch (error) {
        console.log(error);
    }
}

//
async function refreshToken() {
    try {
        if (store.state.authToken !== "") { //갱신 요청을 할 때는 최소한 만료된 토큰이 있어야 한다. 
            const response = await axios.get("/member/refreshToken"); //spring의 경로에 저장된 경로 요청
            store.dispatch("saveAuth", {
                userId: response.data.mid,
                authToken: response.data.accessToken
            }); //store에 다음과 같은 형식으로 받는다. 
            return true; //성공시 ture return
        }
    } catch (error) {
        console.log(error);
    }
    return false; //실패시 False
        
}


export default {
    join,
    login,
    logout
};