import { createStore } from 'vuex'
import counter from "./counter";
import axiosConfig from "@/apis/axiosConfig";

//store 생성 및 내보내기
export default createStore({
    //루트 상태 정의
    state: {
      userId: "",
      authToken:""
    },
    //루트 상태 값을 읽는 메소드(getter) 정의
    getters: {
        getUserId(state, getters, rootState, rootGetters) {
            return state.userId;
        },
        getAuthToken(state, getters, rootState, rootGetters) {
            return state.authToken;
        },
    },

    //루트 상태 값을 변화시키는 메소드(setter) 정의(동기 방식)
    mutations: {
        setUserId(state, payload) {
            state.userId = payload;
        },
        setAuthToken(state, payload) {
            state.authToken = payload;
        },
    },
    //비동기 작업을 실행하고 결과에 따라 상태 값을 변화시키는 메소드
    actions: {
        //payload: {userId:"xxxx", duration:3000}
        setUserIdByAsync(context, payload) {
            new Promise((resolve, reject) => {
                //시간을 요하는 작업(주로 Back-end로 데이터 요청)
                setTimeout(() => {
                    //성공적으로 응답을 얻었을 경우
                    resolve(payload.userId);
                }, payload.duration);
            })
                .then((data) => {
                    //resolve가 되었을 경우 mutations을 이용해서 상태값 변경
                    context.commit("setUserId", data);
                    console.log("userId 상태 변경 성공");
                })
                .catch((error) => {
                    //예외가 발생했거나, reject된경우
                    console.log("userId 상태 변경 실패");
                });
      },

      loadAuth(context, payload) { //세션 스토리지의 값들을 다시 가져온다. 
        context.commit("setUserId", sessionStorage.getItem("userId") || "");
        context.commit("setAuthToken", sessionStorage.getItem("authToken") || "");
        if (context.state.authToken !== "") { //값이 없으면 토큰 값을 넣어주기 위하여 
          axiosConfig.addAuthHeader(context.state.authToken);
        
        
        
        }
        

      },
        //로그인 성공했을 때 실행
        //payload: {{userId: xxx, authtoken: yyyy}} 의 형태를 가진다.
        saveAuth(context, payload) {
            context.commit("setUserId", payload.userId); //헤드에 저장하여 값을 지속적으로 사용한다. 
          context.commit("setAuthToken", payload.authToken); //
          sessionStorage.setItem("userId", payload.userId); //로그인을 하면 아이디를 키로 하여 세션 스토리지에 저장한다. 
          sessionStorage.setItem("authToken", payload.authToken);
            axiosConfig.addAuthHeader(payload.authToken); //모든 요청시 인증 토큰을 헤드에 실어서 보낸다. 
      },
        // context 객체는 store의 메소드와 속성들을 가지고 있는 객체

        //로그아웃 할 때 실행
        deleteAuth(context, payload) {
            context.commit("setUserId", "");
          context.commit("setAuthToken", "");
          sessionStorage.removeItem("userId", payload.userId); //로그인을 하면 아이디를 키로 하여 세션 스토리지에 저장한다.
          sessionStorage.removeItem("authToken", payload.authToken);
          axiosConfig.removeAuthHeader(); //인증 토큰 헤드에서 삭제
        }
    },
    modules: {
        counter,
    },
});
